<template>
	<div class="add_rank">
		<current yiji="参数管理" erji="业务参数" sanji="职级薪酬标准设置" siji="职级薪酬标准新增"></current>
		<div class="content">
			<div class="title">
				<span class="title-text">职级薪酬标准新增</span>
				<el-button type="primary" class="conserve" @click="save('formdata')">保存</el-button>
			</div>
			<div class="content-inner">
				<el-form ref="formdata" :rules="rules" :model="formdata" label-width="120px">
					<el-form-item label="公司名称">
						<el-select v-model="formdata.organNo" value-key="organNo">
							<el-option v-for="item in compList" :key="item.organNo" :label="item.organName" :value="item.organNo"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="模版名称" prop="applyName">
						<el-input v-model="formdata.applyName"></el-input>
					</el-form-item>
					<el-form-item label="职级" prop="rank">
						<el-select v-model="formdata.rank">
							<el-option v-for="item in rankList" :key="item" :value="item"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="薪资标准下限" prop="salaryFloor">
						<el-input v-model="formdata.salaryFloor"></el-input>
					</el-form-item>
					<el-form-item label="薪资标准上限" prop="salaryTop">
						<el-input v-model="formdata.salaryTop"></el-input>
					</el-form-item>
					<el-form-item label="出差标准（人/天）" prop="businessStandard">
						<el-input v-model="formdata.businessStandard"></el-input>
					</el-form-item>
					<el-form-item label="备注" prop="remark">
						<el-input type="textarea" v-model="formdata.remark"></el-input>
					</el-form-item>
				</el-form>
			</div>
		</div>
	</div>
</template>

<script>
	import current from '../../common/current_position.vue'
	const baseURL = 'iem_hrm'
	export default {
		data() {
			var checkSalaryTop = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('薪资标准上限不能为空'));
				} else if(!/^\d{1,14}(\.\d{1,2})?$/.test(value)) {
					callback(new Error('请输入正确的金额'));
				}else if(Number(value) <= Number(this.formdata.salaryFloor)) {
					callback(new Error('上限值必须大于下限值!'));
				} else {
					callback();
				}
			};
			var checkSalaryFloor = (rule, value, callback) => {
				if(value === '') {
					callback(new Error('薪资标准下限不能为空'));
				} else if(!/^\d{1,14}(\.\d{1,2})?$/.test(value)) {
					callback(new Error('请输入正确的金额'));
				}else {
					callback();
				}
			};
			
			var checkBusinessStandard = (rule, value, callback) => {
				if(value && !/^\d*$/.test(value)) {
					callback(new Error('请输入出差标准天数'));
				} else {
					callback();
				}
			};
			return {
				formdata: {
					organNo: "",
					compName: '',
					applyName: "",
					rank: '',
					salaryFloor: '',
					salaryTop: '',
					businessStandard: '',
					remark: ""
				},
				rankList: ['B10-高级开发软件工程师', 'B5-中级开发软件工程师', 'B5-UI'],
				//公司列表
				compList: [
					{organName: "上海分公司",organNo: '01'},
					{organName: "魔方分公司深圳分公司",organNo: 'p1'},
					{organName: "深圳前海橙色魔方信息技术有限公司5666666666666",organNo: '0'},
					{organName: "上海魔方分公司",organNo: '1002'},
					{organName: "魔方南山分公司",organNo: '1001'}
				],
				rules: {
					compName: [
						{ required: true, message: '公司名称不能为空', trigger: 'blur' }
					],
					applyName: [
						{ required: true, message: '模版名称不能为空', trigger: 'blur' }
					],
					rank: [
						{ required: true, message: '职级不能为空', trigger: 'blur' }
					],
					salaryFloor: [
						{ required: true, message: '薪资标准下限不能为空', trigger: 'blur' },
						{ pattern: /^\d{1,14}(\.\d{1,2})?$/, message: "请输入正确的金额" }
					],
					salaryTop: [
						{ required: true, validator: checkSalaryTop, trigger: 'blur' }
					],
					businessStandard: [
						{ message: '请输入出差标准天数', trigger: 'blur' },
						{ pattern: /^\d*$/, message: "请输入出差标准天数" }
					],
					remark: [
						{ min: 0, max: 512, message: '长度在 0 到 512 个字符之间', trigger: 'blur' }
					]
				}
			}
		},
		components: {
			current
		},
		created() {
			//查询公司列表
			this.queryCompList();
		},
		methods: {
			save(formName) {
				const self = this;
				self.$refs[formName].validate((valid) => {
					if(valid) {
						let params = {
							organNo: self.formdata.organNo,
							applyName: self.formdata.applyName,
							rank: self.formdata.rank,
							salaryFloor: self.formdata.salaryFloor,
							salaryTop: self.formdata.salaryTop,
							businessStandard: self.formdata.businessStandard,
							remark: self.formdata.remark
						}
						//新增职级模版
						self.addCparm(params);

					} else {
						return false;
					}
				});
			},
			addCparm(params) {
				const self = this;
				self.$axios.post(baseURL + '/RankSalaryTemplate/addCparm', params)
				.then((res) => {
					console.log('addCparm',res);
					self.$message({ message: '操作成功', type: 'success' });
				}).catch((err) => {
					console.log('error')
				})
			},
			queryCompList() {
				let self = this;
				self.$axios.get(baseURL+'/organ/selectCompanyByUserNo')
				.then(function(res) {
					console.log('CompList',res);
					self.compList = res.data.data;
				}).catch(function(err) {
					console.log(err);
				})
			}
		}
	}
</script>

<style>
	.add_rank {
		padding-left: 20px;
		padding-bottom: 20px;
		width: 100%;
	}
	
	.add_rank .content {
		width: 100%;
		min-height: 510px;
		padding: 0px 20px;
		background: #ffffff;
		clear: both;
	}
	
	.add_rank .content .title {
		border-bottom: 1px solid #EEEEEE;
	}
	
	.add_rank .content .title .title-text {
		display: inline-block;
		position: relative;
		padding: 14px 0px;
		font-size: 16px;
		height: 50px;
	}
	
	.add_rank .content .title .title-text:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: -1px;
		width: 100%;
		height: 2px;
		background: #333333;
	}
	
	.add_rank .content-inner {
		padding: 30px 0px;
	}
	
	.add_rank .conserve {
		float: right;
		margin-top: 10px;
		background: #F4F4F4;
		border: 1px solid #F4F4F4;
		border-radius: 0px;
		font-size: 14px;
		color: #333333;
		width: 120px;
		height: 30px;
		padding: 0;
	}
	
	.add_rank .el-form-item {
		margin-bottom: 22px;
		float: left;
		width: 50%;
	}
	
	.add_rank .el-input__inner {
		border: 1px solid #EEEEEE;
		color: #999999;
		width: 300px;
		height: 40px;
		margin-left: 30px;
	}
	.add_rank .el-input__inner:hover {
	    border-color: #FF9900;
	}
	.add_rank .el-form-item__label {
		text-align: right;
		vertical-align: middle;
		float: left;
		font-size: 14px;
		color: #999999;
		line-height: 1;
		padding: 11px 0px 11px 0;
		box-sizing: border-box;
		font-weight: normal;
	}
	
	.add_rank .el-form-item__error {
		padding-left: 30px;
	}
	.add_rank .el-textarea__inner {
		margin-left: 30px;
		border-color: #EEEEEE;
	}
	.add_rank .el-textarea__inner:hover {
	    border-color: #FF9900;
	}
</style>


